import React, { useState, useEffect } from 'react';
import './App.css';

const App = () => {
  const [input, setInput] = useState('');
  const [history, setHistory] = useState([]);
  const [tempResult, setTempResult] = useState('');
  const [isResult, setIsResult] = useState(false);
  
  const processInput = (input) => {
    let replacedInput = input.replace('', '*').replace('', '/');

    // Handle square root
    replacedInput = replacedInput.replace(/(\d*)(\d+(\.\d+)?)/g, (_, num, sqrt) => {
      const number = num ? `${num}*` : '';
      return `${number}Math.sqrt(${sqrt})`;
    });

    // Handle multiple percentages
    while (replacedInput.includes('%')) {
      replacedInput = replacedInput.replace(/(\d+(\.\d+)?)%/, (_, number) => {
        return (parseFloat(number) * 0.01).toString();
      });
    }

    return replacedInput;
  };

  useEffect(() => {
    if (input.includes('+') || input.includes('-') || input.includes('') || input.includes('') || input.includes('%') || input.includes('')) {
      try {
        // eslint-disable-next-line
        setTempResult(eval(processInput(input)));
      } catch (e) {
        setTempResult('');
      }
    } else {
      setTempResult('');
    }
  }, [input]);

  const handleInput = (val) => {
    if (val === '=') {
      try {
        // eslint-disable-next-line
        const result = eval(processInput(input));
        setHistory([...history, `${input} = ${result}`]);
        setInput(result.toString());
        setIsResult(true);
      } catch (e) {
        setInput('Error');
        setIsResult(true);
      }
      return;
    }
    if (val === 'C' || val === 'AC') {
      if (isResult) {
        setInput('');
        setIsResult(false);
      } else {
        setInput('');
        if (val === 'AC') setHistory([]);
      }
      return;
    }
    if (val === '+/-') {
      if (!isResult) setInput(input.slice(0, -1));
      return;
    }
    if (val === '%' && (input === '' || input === '0')) {
      return;
    }
    if ((val === '' || val === '') && (input === '' || input === '0')) {
      setInput('0' + val);
      return;
    }
    if (isResult) {
      setInput(val);
      setIsResult(false);
    } else {
      setInput(input + val);
    }
  };

  const clearButtonLabel = () => {
    if (input || isResult) return 'C';
    return 'AC';
  };

  const getClass = (val) => {
    if (!isNaN(val) || val === '.') return 'number';
    if (val === '=') return 'equal';
    if (['AC', '', '+/-', '%', 'C'].includes(val)) return 'special';
    return 'symbol';
  };

  return (
    <div className="App">
      <div className="calculator">
        <div className="display">
          <div className="history">
            {history.map((item, index) => (
              <div key={index}>{item}</div>
            ))}
          </div>
          <div className="input">
            {input || '0'}
            <div className="temp-result">{tempResult}</div>
          </div>
        </div>
        <div className="buttons">
          {[clearButtonLabel(), '+/-', '%', '', '7', '8', '9', '', '4', '5', '6', '-', '1', '2', '3', '+', '', '0', '.', '='].map((val) => (
            <button key={val} className={getClass(val)} onClick={() => handleInput(val)}>{val}</button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default App;
